import './App.css'
import { useState } from 'react'
import { toBeInTheDocument } from '@testing-library/jest-dom/dist/matchers'

function App() {
  let post = ''
  //
  let [title, titleChange] = useState([
    ' ',
    '  ',
    ' ',
  ])
  let [logo, setLogo] = useState('ReactBlog')
  let [good, goodChange] = useState([0, 0, 0])
  let [input, setInput] = useState('')
  let [modal, setModal] = useState(false)
  let [titleState, titleStateChange] = useState(0)

  return (
    <div className="App">
      <div className="blog">
        <h4 style={{ color: 'red', fontSize: '16px' }}></h4>
      </div>
      {title.map(function (a, i) {
        return (
          <div className="list" key={i}>
            <h4
              onClick={() => {
                setModal(true)
                titleStateChange(i)
              }}
            >
              {title[i]}
              <span
                onClick={(e) => {
                  e.stopPropagation()
                  let copyGood = [...good]
                  copyGood[i] = copyGood[i] + 1
                  goodChange(copyGood)
                }}
              >
                
              </span>{' '}
              {good[i]}{' '}
              <button
                onClick={(e) => {
                  e.stopPropagation()
                  let copy = [...title],
                    copyGood = [...good]
                  copy.splice(i, 1)
                  copyGood.splice(i, 1)
                  titleChange(copy)
                  goodChange(copyGood)
                }}
              >
                
              </button>
            </h4>
            <p>11 7</p>
          </div>
        )
      })}
      <input
        onChange={(e) => {
          //  
          setInput(e.target.value)
        }}
      ></input>
      <button
        onClick={() => {
          titleChange([input, ...title])
          goodChange([0, ...good])
          //titleChange(title.concat(input))  
        }}
      >
        
      </button>
      {modal == true ? (
        <Modal
          color={'skyblue'}
          titleState={titleState}
          title={title}
          titleChange={titleChange}
        />
      ) : null}
    </div>
  )
}

// 1, 
const Modal = (props) => {
  return (
    //prop     
    <div className="modal" style={{ background: props.color }}>
      <h4>{props.title[props.titleState]}</h4>
      <p></p>
      <p></p>
      <button
        onClick={() => {
          let copyTitle = [...props.title]
          copyTitle[0] = '  '
          props.titleChange(copyTitle)
        }}
      >
          
      </button>
    </div>
  )
}
// 2
function Component() {
  return (
    <div className="Component">
      <h4></h4>
      <p></p>
      <p></p>
    </div>
  )
}

export default App
